  let ringMaterial = new THREE.MeshBasicMaterial({
    map: new THREE.TextureLoader().load(ringImg),
    color: new THREE.Color(dvColor.value[0]),//
    transparent: true,
    opacity: 1,
    side: THREE.DoubleSide,
    fog: true,
    depthWrite: false,
    blending: THREE.AdditiveBlending,
  });
  let ringGeometry = new THREE.PlaneGeometry(earthSize * 2, earthSize * 2, 10, 10);
  expandRingMesh = new THREE.Mesh(ringGeometry, ringMaterial);
  expandRingMesh.name = "";
  expandRingMesh.rotation.x = 90 * Math.PI / 180;
  earthGroup.add(expandRingMesh);
};

//
function createExpandRingAnimation() {
  gsap.isTweening(expandRingMesh.scale) ||//
    (gsap.fromTo(
      expandRingMesh.scale,//
      { x: 1, y: 1, },
      { x: 2.7, y: 2.7, duration: 1.5 }
    ),
      gsap.fromTo(
        expandRingMesh.material,//
        { opacity: 1, },
        { opacity: 0, duration: 1.5 }
      ))
};

//
function createSphereGlow() {
  //SpriteMaterial
  let glowMaterial = new THREE.SpriteMaterial({
    map: new THREE.TextureLoader().load(earthGlowImg),
    color: new THREE.Color(dvColor.value[0]),//
    transparent: true,
    opacity: 1,
    side: THREE.DoubleSide,
    fog: true,
    depthWrite: false,
    blending: THREE.AdditiveBlending,
  })
  let glowSprite = new THREE.Sprite(glowMaterial);
  glowSprite.scale.set(earthSize * 3.2, earthSize * 3.2, 1); //
  earthGroup.add(glowSprite);
};

//
function createOrbitControls() {
  let { autoRotate, isDrag, isZoom, rotateSpeed } = JSON.parse(sessionStorage.getItem("config") as any);//sessionStorage
  orbitControls = new OrbitControls(camera, renderer.domElement);
  orbitControls.autoRotate = autoRotate; //()
  orbitControls.enableRotate = isDrag;//
  orbitControls.enableZoom = isZoom; //
  orbitControls.autoRotateSpeed = rotateSpeed / 10; //
  orbitControls.enablePan = false; //
  orbitControls.enableDamping = true; //
  orbitControls.dampingFactor = 0.05; //(.25)
  orbitControls.minDistance = 250; //
  orbitControls.maxDistance = 400; //  
};

//
function render() {
  anId.value = requestAnimationFrame(render);
  document.getElementById("sphereDiv") &&
    document
      .getElementById("sphereDiv")!
      .addEventListener("mousemove", onMousemove, false);
  orbitControls.update(); //
  dotLineRingMesh && (dotLineRingMesh.rotation.y += 5);//
  flylineMesh && (flylineMesh.rotation.z -= .15);//
  createExpandRingAnimation();//
  renderer.render(scene, camera);
};

//(vuedata)
function onMousemove(e: any) {
  let dom = document.getElementById("sphereDiv");
  let width = dom!.clientWidth; //
  let height = dom!.clientHeight; //
  //threejs
  mouse.x = (e.offsetX / width) * 2 - 1;
  mouse.y = -(e.offsetY / height) * 2 + 1;
  raycaster.setFromCamera(mouse, camera); //raycaster
  let intersects = raycaster.intersectObjects(scene.children); // raycaster
  if (intersects.length !== 0 && intersects[0].object.name == "") {
    (JSON.parse(sessionStorage.getItem("config") as any).isTag) && (currentPointData.value = intersects[0].object.dotData); //intersects
    dom!.style.cursor = "pointer"; //
    position.value = {
      x: e.pageX + 20 + "px",
      y: e.pageY + "px",
    }; //
    // intersects[0].object.isHover = true;
    // console.log(intersects[0].object);
  } else {
    currentPointData.value = {}; //
    dom!.style.cursor = "move"; //
  }
  // console.log(scene.children.filter((c: any) => { return c.name == "" }))
};

//
function initEchart() {
  let sortList: any = sortFun(sphereData.value);//
  histogramChartFun(sortList.slice(0, sliceNum)); //
  certain.value = Number(allData.value.othertotal.certain);//
  addcure.value = Number(allData.value.othertotal.recure);///
  addDie.value = Number(allData.value.othertotal.die);///
}

//()
function sortFun(arr: any) {
  arr.forEach((a: any, index: number) => {
    for (let j = 0; j < arr.length - 1 - index; j++) {
      if (Number(arr[j].value) < Number(arr[j + 1].value)) {
        let tmp = arr[j];
        arr[j] = arr[j + 1];
        arr[j + 1] = tmp;
      }
    }
  });
  return arr;
};

//
function histogramChartFun(list: any) {
  let chartDom = document.getElementById("histogramDiv");
  (histogramChart) && (histogramChart.dispose());//
  histogramChart = echarts.init(chartDom);
  let option: any = {
    backgroundColor: "",
    title: {
      left: "center",
      top: "3%",
      textStyle: {
        color: "#fff",
      },
    },
    grid: {
      top: "0%",
      left: "25%",
      right: "5%",
      bottom: "0%",
    },
    xAxis: {
      type: "value",
      show: false,
    },
    yAxis: {
      type: "category",
      axisLabel: {
        color: "#fff",
      },
      data: [],
    },
    tooltip: {
      trigger: "axis",
      axisPointer: {
        type: "shadow",
      },
      backgroundColor: "rgba(0,0,0,.5)",
      textStyle: {
        color: "#fff",
        fontWeight: "bolder"
      },
      borderWidth: "0",
    },
    series: [
      {
        data: [],
        type: "bar",
        animationDuration: 2000,//
        animationEasing: "cubicInOut",//
        showBackground: true,
        backgroundStyle: {
          color: sysBackgroundColor,
        },
        itemStyle: {
          color: new echarts.graphic.LinearGradient(0, 1, 1, 1, [
            {
              offset: 0,
              color: dvColor.value[0]
            },
            {
              offset: 1,
              color: "#555"
            }
          ])
        },
        label: {
          color: "#fff",
          fontWeight: "bolder",
          show: true,
          align: "left",
          formatter: "{c}",
        },
      },
    ],
  };
  list.forEach((l: any) => {
    option.yAxis.data.push(l.name);
    option.series[0].data.push(l.value);
  });
  option.yAxis.data.reverse();
  option.series[0].data.reverse();
  option && histogramChart.setOption(option);
}

//ip
function getLocationMsg() {
  if (dataType.value == "") {
    let jsonpUrl: any = process.env.VUE_APP_3;
    jsonp(jsonpUrl, (res: any) => {
      userMsg.value = res;
      getProvinceData();//
    })
  } else {
    userMsg.value = tempIpData;
    getProvinceData();//
  };

};

//
function getProvinceData() {
  let pro = userMsg.value.pro;//
  let ePro = "";//
  if (dataType.value == "") {
    allData.value.list.forEach((l: any) => {
      if (pro.search(l.name) >= 0) {
        ePro = l.ename;//
      }
    })
    jsonp1(
      process.env.VUE_APP_5,
      (res: any) => {
        currentProvinceData.value = res.data;//
      },
      "val1",
      "mod=province&province=" + ePro
    );
  } else {
    currentProvinceData.value = tempProvinceData.data;
  }
}

//
function provinceAnalyze() {
  isProvinceEchartDrawer.value = true;
}

//
async function openPreview() {
  isLoading.value = true;
  let tempData: any = {};//
  await (tempData = JSON.parse(JSON.stringify(currentProvinceData.value)));
  let currentDate: any = currentProvinceData.value.cachetime.split(" ")[0];//
  let wordData: any = {
    wordName: "",//
    hasCityData: true,//
    currentDate: currentDate,//
    version: version.value,//
    currentCityData: {},//
    overviewData: {
      name: tempData.province,
      contotal: tempData.contotal,//
      econNum: tempData.econNum,//
      curetotal: tempData.curetotal,//
      deathtotal: tempData.deathtotal,//
      conadd: tempData.conadd ? tempData.conadd : 0//
    },
    tabData: [],//
    sourceUrl: window.location.href//url
  };
  await tempData.city.forEach((c: any, index: any) => {
    c.index = index + 1;//
    wordData.tabData.push(c);//
    if (userMsg.value.city.search(c.name) !== -1) {
      wordData.hasCityData = true;
      wordData.currentCityData = c;//
      wordData.wordName = c.name;//
    }
  })
  //
  if (!wordData.wordName) {
    wordData.hasCityData = false;
    wordData.wordName = wordData.overviewData.name;
  }
  await getWordBlob("docx/word.docx", wordData).
    then((res: any) => {
      isLoading.value = false;
      reportData.value = {
        fileName: wordData.wordName + ".docx",//
        blobData: res,//blob
      };
      isReport.value = true;//
    })
};


