import * as XLSX from "xlsx";
import { saveAs } from 'file-saver'

//
let eg = {
    fileName: "",//
    tabHead: ["", "", ""],//
    keyList: ["name", "population", "test"],//,
    tabData: [
        { name: "", population: "11", test: "t1" },
        { name: "", population: "22", test: "t2" },
        { name: "", population: "35", test: "t3" }
    ]//
}

function workbook2blob(workbook: any) {
    // excel
    let wopts: any = {
        // 
        bookType: "xlsx",
        // Shared String TableIOS
        bookSST: false,
        type: "binary"
    };
    var wbout = XLSX.write(workbook, wopts);
    // ArrayBuffer
    function s2ab(s: any) {
        var buf = new ArrayBuffer(s.length);
        var view = new Uint8Array(buf);
        for (var i = 0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xff;
        return buf;
    }
    let buf = s2ab(wbout);
    var blob = new Blob([buf], {
        type: "application/octet-stream"
    });
    return blob;
};

//
export default async function downloadXlsx(tabObj: any) {
    let aoaList: any = [];
    aoaList[0] = tabObj.tabHead; //
    tabObj.tabData.forEach((tabItem: any, tabIndex: number) => {
        aoaList[tabIndex + 1] = [];//
        tabObj.keyList.forEach((keyItem: any, keyIndex: number) => {
            let val = tabItem[keyItem];//
            ((typeof val == "undefined") || (val == "")) ?
                (aoaList[tabIndex + 1][keyIndex] = "-") ://"-"
                (aoaList[tabIndex + 1][keyIndex] = val + "");//
        })
    });
    let workSheet = null;
    workSheet = XLSX.utils.aoa_to_sheet(aoaList); //
    let workBook = XLSX.utils.book_new(); //
    XLSX.utils.book_append_sheet(workBook, workSheet, "1"); //
    //XLSXwriteFile
    // await XLSX.writeFile(workBook, tabObj.fileName + ".xlsx"); 
    //:file-saversaveAsBlob
    saveAs(
        new Blob([workbook2blob(workBook)], { type: "application/octet-stream" }),
        tabObj.fileName + ".xlsx"
    );
};